* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

:root {
  --bg: #e3e4e8;
  --fg: #17181c;
  --input: #fff;
  --primary: #255ff4;
  --dur: 1s;
  font-size: calc(.833333vw + 13.3333px);
}

body, input {
  color: var(--fg);
  font: 1em / 1.5 Hind, sans-serif;
}

body {
  background: var(--bg);
  height: 100vh;
  display: flex;
}

form, input, .caret {
  margin: auto;
}

form {
  width: 100%;
  max-width: 17em;
  position: relative;
}

input, .caret {
  transition: all calc(var(--dur) * .5) linear;
  display: block;
}

input {
  caret-color: var(--primary);
  width: 2em;
  height: 2em;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: none;
  border-radius: 50%;
  box-shadow: inset 0 0 0 .25em;
}

input:focus, input:valid {
  background: var(--input);
  box-shadow: none;
  transition-duration: calc(var(--dur) * .25);
  transition-delay: calc(var(--dur) * .25);
  width: 100%;
  height: 3em;
  border-radius: .25em;
  padding: .75em 1em;
}

input:focus {
  animation: showCaret var(--dur) steps(1);
  outline: rgba(0, 0, 0, 0);
}

input:focus + .caret, input:valid + .caret {
  animation: handleToCaret var(--dur) linear;
  width: 1px;
  height: 1.5em;
  background: none;
  transform: translate(0, -1em)rotate(-180deg)translate(7.5em, -.25em);
}

input::-webkit-search-decoration {
  -webkit-appearance: none;
}

label {
  color: #e3e4e8;
  width: 0;
  height: 0;
  position: absolute;
  overflow: hidden;
}

.caret {
  width: .25em;
  height: 1em;
  transform-origin: 50% 0;
  background: currentColor;
  border-radius: 0 0 .125em .125em;
  margin-bottom: -.6em;
  transform: translate(0, -1em)rotate(-45deg)translate(0, .875em);
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: #17181c;
    --fg: #e3e4e8;
    --input: #2e3138;
    --primary: #5583f6;
  }
}

@keyframes showCaret {
  from {
    caret-color: rgba(0, 0, 0, 0);
  }

  to {
    caret-color: var(--primary);
  }
}

@keyframes handleToCaret {
  from {
    width: .25em;
    height: 1em;
    background: currentColor;
    transform: translate(0, -1em)rotate(-45deg)translate(0, .875em);
  }

  25% {
    width: .25em;
    height: 1em;
    background: currentColor;
    transform: translate(0, -1em)rotate(-180deg)translate(0, .875em);
  }

  50%, 62.5% {
    background: var(--primary);
    width: 1px;
    height: 1.5em;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, 2.5em);
  }

  75%, 99% {
    background: var(--primary);
    width: 1px;
    height: 1.5em;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, -.25em);
  }

  87.5% {
    background: var(--primary);
    width: 1px;
    height: 1.5em;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, .125em);
  }

  to {
    width: 1px;
    height: 1.5em;
    background: none;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, -.25em);
  }
}

